/* Global Styles */
* {
  box-sizing: border-box;
}
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
}

/* Custom Scrollbar */
body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  background: var(--scrollbar-color);
}

body::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb-color);
  border-radius: 6px;
  border: 3px solid var(--scrollbar-color);
}

.App {
  text-align: center;
}

/* Light Theme Variables */
:root {
  --background-color: #e4e3e3;
  --text-color: #000;
  --not-text: #000;
  --task-background-color: #a2dff2;
  --completed-task-background-color: #e0ffe0;
  --not-completed-color: #f3b2b2;
  --border-color: #ddd;
  --input-background-color: #fff;
  --button-background-color: #306100;
  --button-text-color: #fff;
  --delete-btn-color: #1a1a1a;
  --scrollbar-color: #fff5f5;
  --scrollbar-thumb-color: #888;
}

/* Dark Theme Variables */
[data-theme="dark"] {
  --background-color: #545151;
  --text-color: #f9f9f9;
  --task-background-color: #2896;
  --completed-task-background-color: #4caf50;
  --border-color: #555;
  --not-text: #fff;
  --not-completed-color: #ff6666;
  --input-background-color: #444;
  --button-background-color: #4caf50;
  --button-text-color: #fff;
  --delete-btn-color: #fff;
  --scrollbar-color: #939292;
  --scrollbar-thumb-color: #666;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* To-Do List Container */
#todo-list {
  width: 800px;
  margin: 20px 10px;
  background-color: var(--task-background-color);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Completed Task */
.task.completed {
  background-color: var(--completed-task-background-color);
}

/* Heading */
#todo-list h2 {
  color: var(--text-color);
  border: none;
}

/* Task */
.task {
  display: flex;
  justify-content: space-between;
  text-align: left;
  align-items: center;
  width: 100%;
  padding: 12px;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  margin-bottom: 12px;
  background-color: var(--not-completed-color);
  color: var(--not-text);
}

/*modify*/
.modify {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Checkbox */
.task input[type="checkbox"] {
  padding: 5px;
  width: 20px;
  height: 20px;
  margin-right: 12px;
  color: green;
}

.strikethrough {
  text-decoration: line-through;
}

/* Delete Button */
.delete-btn {
  cursor: pointer;
  color: var(--delete-btn-color);
  font-weight: bold;
  display: block;
  margin-left: 8px;
  font-size: 20px;
}

/* Input */
#task-input {
  flex: 1;
  padding: 12px 8px;
  margin-right: 8px;
  background-color: var(--input-background-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-color);
  margin-top: 10px;
}

/* Add Button */
#add-btn {
  padding: 12px;
  background-color: var(--button-background-color);
  color: var(--button-text-color);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

#add-btn:hover {
  background-color: #6aa330;
}

/*Theme container*/
.theme-container {
  width: 100%;
  height: 50px;
  display: block;
}
/* Add Task Container */
#add-task-container {
  margin-bottom: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}

/* Tasks Container */
#tasks-container {
  width: 800px;
  margin-bottom: 12px;
  -webkit-user-select: none;
  user-select: none;
}

@media (max-width: 728px) {
  #todo-list {
    width: 90vw;
  }
  #tasks-container {
    width: 90vw;
  }
}

/* Empty Note */
#empty-note {
  text-align: center;
  font-weight: bold;
  margin: 20px auto;
}

/* Theme Toggle Button */
#theme-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 10px 12px;
  background-color: var(--button-background-color);
  color: var(--button-text-color);
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
